<template>
  <div class="body">
    <el-container>
      <el-header>
        <cmdb-header :tab-date="tabDate"></cmdb-header>
      </el-header>
      <el-container class="content-container" v-bind:class="{ active: isActive}">
        <el-aside width="200px" v-show="isShowAside">
          <cmdb-side></cmdb-side>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import service from "../scripts/service.js";
import cmdbHeader from "../components/header";
import cmdbSide from "../components/aside";
import "../styles/style.css";
export default {
  data: function() {
    return {
      tabDate: [
        {
          value: "1",
          label: "首页"
        },
        {
          value: "2",
          label: "实习考核"
        },
        {
          value: "3",
          label: "实习周报"
        },
        {
          value: "4",
          label: "个人中心"
        },
        {
          value: "5",
          label: "实习课题"
        },
        {
          value: "6",
          label: "考核中心"
        }
      ],
      isShowAside: false,
      isActive: false
    };
  },
  watch: {
    $route(newVal, oldVal) {
      if (
        newVal.path == "/index" ||
        newVal.path == "/weeklyModify" ||
        newVal.path == "/weeklyDetail" ||
        newVal.path == "/weeklyCreat" ||
        newVal.path == "/examineDetail" ||
        newVal.path == "/examineCreat" ||
        newVal.path == "/examineModify"
      ) {
        this.isShowAside = false;
        this.isActive = false;
      } else if (newVal.path == "/project") {
        this.isShowAside = false;
        this.isActive = true;
      } else {
        this.isShowAside = true;
        this.isActive = false;
      }
    }
  },
  mounted: function() {},
  methods: {},
  components: {
    cmdbHeader,
    cmdbSide
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content-container {
  position: absolute;
  top: 55px;
  bottom: 0;
  left: 0;
  right: 0;
  min-width: 1150px;
}
</style>







