<template>
  <div>
    <div class="title fl">实习生考核系统</div>
     <el-tabs class="fl" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane :key="index" v-for="(item,index) in tabDate" :label="item.label" :name="index"></el-tab-pane>
    </el-tabs>
    <!-- 登录注册 -->
    <div class="fr">
      <el-dropdown>
        <el-button type="primary">
          <i class="fa fa-user-circle-o" aria-hidden="true"></i><i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item @click.native="changePwd">修改密码</el-dropdown-item>
          <el-dropdown-item @click.native="editStaffInfo">修改信息</el-dropdown-item>
          <el-dropdown-item @click.native="userLogout">退出系统</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
import service from "../scripts/service.js";
export default {
  props: ["tabDate"],
  data() {
    return {
      activeName: "0"
    };
  },
  mounted: function() {},
  methods: {
    handleClick: function(activeName) {
      var name = activeName.label;
      if ("考核中心" == name) {
        this.$router.push({
          path: "/examineInformation"
        });
      } else if ("实习课题" == name) {
        this.$router.push({
          path: "/project"
        });
      } else if ("个人中心" == name) {
        this.$router.push({
          path: "/personalweekly"
        });
      } else if ("实习周报" == name) {
        this.$router.push({
          path: "/weeklyMy"
        });
      } else if ("实习考核" == name) {
        this.$router.push({
          path: "/assessFrontplan"
        });
      } else if ("首页" == name) {
        this.$router.push({
          path: "/index"
        });
      }
    }
  },
  components: {},
  created() {
    this.activeName = "0";
  },
  watch: {
    "$route.path": function(newVal, oldVal) {
      if (newVal.indexOf("/per") != -1) {
        this.activeName = 3;
      } else if (newVal.indexOf("/ass") != -1) {
        this.activeName = 1;
      } else if (newVal.indexOf("/wee") != -1) {
        this.activeName = 2;
      } else if (newVal.indexOf("/project") != -1) {
        this.activeName = 4;
      } else if (newVal.indexOf("/examine") != -1) {
        this.activeName = 5;
      } else {
        this.activeName = "0";
      }
    }
  }
};
</script>
<style scoped>
.title {
  margin-right: 150px;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  color: #ffffff;
}
</style>


