<template>
  <div>
    <el-menu default-active="1" @open="handleOpen" ref="elMenu">
      <el-menu-item v-for="(item,index) in list" :key="item.menuIndex" :index="item.menuIndex" @click="showRightTab(item.menuPath)">
        <span slot="title">{{ item.menuName }}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>
<script>
  import service from "../scripts/service.js";
  export default {
    data() {
      return {
        defaultActive: "1",
        list: [],
        auditFlag: ""
      };
    },
    methods: {
      //展示右边组件
      showRightTab(reqPath) {
        this.$router.push({
          path: reqPath
        });
      }
    },
    watch: {},
    mounted: function() {},
    components: {},
    watch: {
      "$route.path": function(newVal, oldVal) {
        if (newVal.indexOf("/personal") != -1) {
          this.list = [{
              menuIndex: "1",
              menuName: "我的周报",
              menuPath: "/personalWeekly"
            },
            {
              menuIndex: "2",
              menuName: "我收藏的周报",
              menuPath: "/personalCollect"
            },
            {
              menuIndex: "3",
              menuName: "个人信息",
              menuPath: "/personalInfo"
            },
            {
              menuIndex: "4",
              menuName: "请假管理",
              menuPath: "/personalLeave"
            }
          ];
          if (newVal == "/personalWeekly") {
            this.$refs.elMenu.activeIndex = "1";
          } else if (newVal == "/personalCollect") {
            this.$refs.elMenu.activeIndex = "2";
          } else if (newVal == "/personalInfo") {
            this.$refs.elMenu.activeIndex = "3";
          } else if (newVal == "/personalLeave") {
            this.$refs.elMenu.activeIndex = "4";
          }
        } else if (newVal.indexOf("/weekly") != -1) {
          this.list = [{
              menuIndex: "1",
              menuName: "实习周报",
              menuPath: "/weeklyMy"
            },
            {
              menuIndex: "2",
              menuName: "优秀周报推荐",
              menuPath: "/weeklyExcellent"
            }
          ];
          if (newVal == "/weeklyMy") {
            this.$refs.elMenu.activeIndex = "1";
          } else if (newVal == "/weeklyExcellent") {
            this.$refs.elMenu.activeIndex = "2";
          }
        } else if (newVal.indexOf("/examineInformation") != -1) {
          this.list = [{
            menuIndex: "1",
            menuName: "人员基本信息",
            menuPath: "/examineInformation"
          }];
          if (newVal == "/examineInformation") {
            this.$refs.elMenu.activeIndex = "1";
          }
        } else if (newVal.indexOf("/ass") != -1) {
          // else if (newVal.indexOf("/project") != -1) {
          //   //资产审批
          //   this.list = [
          //     {
          //       menuIndex: "1",
          //       menuName: "实习课题",
          //       menuPath: "/project"
          //     }
          //   ];
          //   if (newVal == "/project") {
          //     this.$refs.elMenu.activeIndex = "1";
          //   }
          // }
          //资源预警
          this.list = [{
              menuIndex: "1",
              menuName: "前台培养计划",
              menuPath: "/assessFrontplan"
            },
            {
              menuIndex: "2",
              menuName: "后台培养计划",
              menuPath: "/assessBackplan"
            },
            {
              menuIndex: "3",
              menuName: "工程培养计划",
              menuPath: "/assessProject"
            }
          ];
          if (newVal == "/assessFrontplan") {
            this.$refs.elMenu.activeIndex = "1";
          } else if (newVal == "/assessBackplan") {
            this.$refs.elMenu.activeIndex = "2";
          } else if (newVal == "/assessProject") {
            this.$refs.elMenu.activeIndex = "3";
          }
        }
      }
    }
  };
</script>
<style>
  .el-menu-item,
  .el-submenu__title,
  .el-submenu .el-menu-item {
    height: 40px !important;
    line-height: 40px !important;
    color: #666666 !important;
    font-size: 14px;
  }
  .el-aside>div,
  .el-aside>div>ul {
    height: 100%;
  }
  .el-menu-item.is-active {
    color: #666666 !important;
    border-right: 3px solid rgba(24, 144, 255, 1);
    background-color: rgba(230, 247, 255, 1);
  }
</style>


